.taroify-fixed-nav {
  position: fixed;
  z-index: var(--fixed-nav-z-index, 1010);
  display: inline-block;
  height: var(--fixed-nav-height, 100px);
  right: 0;
}
.taroify-fixed-nav_btn {
  box-sizing: border-box;
  position: absolute;
  right: 0;
  z-index: var(--fixed-nav-z-index, 1010);
  width: 140px;
  height: 100%;
  background: var(--fixed-nav-button-background, var(--primary-color, #1989fa));
  border-radius: 90px 0 0 90px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  display: flex;
  align-items: center;
  justify-content: center;
}
.taroify-fixed-nav_btn .taroify-fixed-nav_btn-icon {
  margin-right: 10px;
  transform: rotate(0deg);
  transition: all 0.3s;
  transform-origin: center;
  line-height: normal;
}
.taroify-fixed-nav_btn-text {
  width: 48px;
  line-height: 26px;
  font-size: var(--fixed-nav-button-font-size, 24px);
  color: #fff;
  flex-shrink: 0;
}
.taroify-fixed-nav_content {
  position: absolute;
  right: 0;
  transform: translateX(100%);
  transition: all 0.5s;
  z-index: var(--fixed-nav-z-content-index, 1009);
  flex-shrink: 0;
  height: 100%;
  background: var(--fixed-nav-background-color, var(--white, #fff));
  display: flex;
  justify-content: space-between;
  border-radius: 50px 0 0 50px;
  box-shadow: 4px 4px 16px 0 rgba(0, 0, 0, 0.2);
  padding: 0 160px 0 40px;
}
.taroify-fixed-nav_content--item {
  position: relative;
  flex: 1;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-width: 100px;
  flex-shrink: 0;
  color: var(--fixed-nav-color, var(--gray-8, #323233));
}
.taroify-fixed-nav_content--img {
  width: 40px;
  height: 40px;
  margin-bottom: 4px;
}
.taroify-fixed-nav_content--text {
  font-size: 20px;
}
.taroify-fixed-nav--active .taroify-fixed-nav_btn-icon {
  transform: rotate(180deg);
}
.taroify-fixed-nav--active .taroify-fixed-nav_content {
  transform: translateX(0%) !important;
}
.taroify-fixed-nav--active .taroify-fixed-nav--left .taroify-icon {
  transform: rotate(0deg) !important;
}
.taroify-fixed-nav--left {
  right: auto;
  left: 0;
}
.taroify-fixed-nav--left .taroify-fixed-nav_btn {
  flex-direction: row-reverse;
  right: auto;
  left: 0;
  border-radius: 0 90px 90px 0;
}
.taroify-fixed-nav--left .taroify-fixed-nav_btn-icon {
  transform: rotate(180deg);
  margin-left: 10px;
}
.taroify-fixed-nav--left .taroify-fixed-nav_content {
  transform: translateX(-100%);
  right: auto;
  border-radius: 0 50px 50px 0;
  padding: 0 40px 0 160px;
}